<template>
  <div class="doc">
    {{params}}
    <div class="loading" v-if="loading">loading</div>
    <div class="container">
      <div class="item" v-for="item in tourism" :key="item.ID">
        {{item.Name}}
        <div v-if="item.Description">
          {{item.Description}}
        </div>
        <div v-else>
          {{item.DescriptionDetail}}
        </div>
      </div>
    </div>
    <div  >
      <div class="bottom" v-show="nodata">
            我是頁尾
      </div>
      <h1 v-show="!nodata"> 
        沒有資料了
      </h1>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return{
      loading:false,
      tourism:[],
      nodata:true
    }
  },
  props:[
    'params'
  ],
  mounted(){
    let key=this.params
    this.call(key)

    this.obs()
  },
  methods:{
    call(val){
      console.log(val)
      if(this.loading) return
      this.loading=true
      setTimeout(()=>{
        //dev
        /*axios.get(`/ScenicSpot/api${val}`,{
          params:{
            '$top':'30',
            '$format':'JSON'
          }
        })*/
        axios.get(`https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot/${val}`,{
          params:{
            '$top':'30',
            '$format':'JSON'
          }
        })
        
        .then(res=>{
        console.log(res.data)
        this.tourism=res.data
        this.loading=false
      })
      },2000)
    },
    test(){
      this.nodata=false
      console.log('dofalse')

      },
    obs(){
      let io =new IntersectionObserver(entries=>{
      // console.log(entries[0].boundingClientRect["y"])
      // console.log(entries)
      let height=window.innerHeight-100
      // console.log(height)
      if(entries[0].intersectionRatio==1&&entries[0].boundingClientRect['y']>height){
        if(this.loading) return
        this.loading=true
        setTimeout(()=>{
          this.loadMore(this.params)
          this.loading=false
        },4000)


      }
    },{threshold:[1]})
    io.observe(document.querySelector('.bottom'))

    },

    loadMore(val){
      let length=this.tourism.length
      if(length%30!=0){
        this.test()
      }
      console.log(length)
      // dev
      /*
      axios.get(`/ScenicSpot/api${val}`,{
        params:{
        $top:'30',
        $skip:`${length}`,
        $format:'JSON',

      }}).then(res=>{
          this.tourism=this.tourism.concat(res.data)
        
      }).catch(err=>{
        console.log(err)
      })
      */
      //production
      axios.get(`https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot/${val}`,{
        params:{
        $top:'30',
        $skip:`${length}`,
        $format:'JSON',

      }}).then(res=>{
          this.tourism=this.tourism.concat(res.data)
        
      }).catch(err=>{
        console.log(err)
      })
    }
  },
  watch:{
    $route(){
      if(this.params){
        this.$emit('update',this.params)
      }
      if(!this.nodata){
        this.nodata=true
      }
      this.tourism=[]
      let params=this.$route.params.id
      this.params=params
      this.call(params)
    }
  }
}
</script>

<style >
    *{
      margin: 0;
      padding: 0;
      list-style: none;

    }
    body{
      position: relative;
    }
      .bottom{
      margin-bottom: 20px;
      opacity: 0;
      /* position: absolute;
      bottom: 0px;
      left: 0px; */
    }
        .loading{
      position: fixed;
      background-color: #fa0;
    }

    .container{
      width: 960px;
      margin: 10px auto;
      display: flex;
      flex-wrap: wrap;
    }
    .item{
      width: 280px;
      padding: 10px;
      margin: 20px 10px;
      height: 300px;
      overflow: hidden;
    }
</style>